import React, { useState } from "react";
import { FaArrowRight, FaEye, FaEyeSlash } from "react-icons/fa";

const AccountCreationPage = () => {
  const [formData, setFormData] = useState({
    username: "",
    email: "",
    password: "",
    confirmPassword: "",
  });
  const [showPassword, setShowPassword] = useState(false);
  const [showConfirmPassword, setShowConfirmPassword] = useState(false);
  const [isAgreed, setIsAgreed] = useState(false);

  const handleInputChange = (e) => {
    setFormData({
      ...formData,
      [e.target.name]: e.target.value,
    });
  };

  const handleSubmit = () => {
    console.log("Form submitted:", formData);
  };

  return (
    <div className="min-h-screen bg-black text-white flex">
      {/* Left Section */}
      <div className="flex-1 relative flex items-center justify-center p-8">
        {/* Simple wave background */}
        <div className="absolute inset-0 overflow-hidden">
          <svg
            className="absolute w-full h-full opacity-20"
            viewBox="0 0 1000 1000"
            preserveAspectRatio="none"
          >
            <defs>
              <linearGradient
                id="waveGradient"
                x1="0%"
                y1="0%"
                x2="100%"
                y2="100%"
              >
                <stop offset="0%" stopColor="#00d4aa" />
                <stop offset="100%" stopColor="#00bcd4" />
              </linearGradient>
            </defs>
            <path
              d="M0,300 Q250,100 500,300 T1000,300 L1000,1000 L0,1000 Z"
              fill="url(#waveGradient)"
            />
            <path
              d="M0,400 Q200,250 400,400 T800,400 T1200,400 L1200,1000 L0,1000 Z"
              fill="url(#waveGradient)"
              opacity="0.3"
            />
          </svg>
        </div>

        <div className="relative z-10 max-w-lg">
          <button className="mb-8 px-4 py-2 border border-gray-600 rounded-full text-white hover:border-cyan-400 transition-colors flex items-center gap-2">
            <FaArrowRight className="transform rotate-180" />
            Back to Website
          </button>

          <div className="mb-6">
            <h1 className="text-5xl font-light mb-2 text-white">Welcome to</h1>
            <h2 className="text-5xl font-light text-white">
              Fortinet.Shopping
            </h2>
          </div>

          <p className="text-gray-300 text-base leading-relaxed">
            Create your account to explore top-tier cybersecurity products,
            enjoy exclusive deals, and manage your orders seamlessly.
          </p>
        </div>
      </div>

      {/* Right Section */}
      <div className="flex-1 flex items-center justify-center p-8 bg-gray-50">
        <div className="w-full max-w-md">
          <div className="text-center mb-8">
            <h3 className="text-3xl font-normal text-gray-800 mb-2">
              Create an Account
            </h3>
            <p className="text-gray-600">
              Already have an Account?
              <a
                href="/login"
                className="text-teal-600 hover:text-teal-700 ml-1"
              >
                Log In
              </a>
            </p>
          </div>

          <div className="space-y-4">
            {/* Username Field */}
            <div>
              <input
                type="text"
                name="username"
                placeholder="Username"
                value={formData.username}
                onChange={handleInputChange}
                className="w-full px-4 py-3 border border-gray-300 rounded-lg focus:border-teal-500 focus:outline-none text-gray-800 placeholder-gray-500"
              />
            </div>

            {/* Email Field */}
            <div>
              <input
                type="email"
                name="email"
                placeholder="Email"
                value={formData.email}
                onChange={handleInputChange}
                className="w-full px-4 py-3 border border-gray-300 rounded-lg focus:border-teal-500 focus:outline-none text-gray-800 placeholder-gray-500"
              />
            </div>

            {/* Password Field */}
            <div className="relative">
              <input
                type={showPassword ? "text" : "password"}
                name="password"
                placeholder="Enter Your Password"
                value={formData.password}
                onChange={handleInputChange}
                className="w-full px-4 py-3 pr-12 border border-gray-300 rounded-lg focus:border-teal-500 focus:outline-none text-gray-800 placeholder-gray-500"
              />
              <button
                type="button"
                onClick={() => setShowPassword(!showPassword)}
                className="absolute right-4 top-1/2 transform -translate-y-1/2 text-gray-500 hover:text-gray-700"
              >
                {showPassword ? <FaEyeSlash /> : <FaEye />}
              </button>
            </div>

            {/* Confirm Password Field */}
            <div className="relative">
              <input
                type={showConfirmPassword ? "text" : "password"}
                name="confirmPassword"
                placeholder="Confirm Password"
                value={formData.confirmPassword}
                onChange={handleInputChange}
                className="w-full px-4 py-3 pr-12 border border-gray-300 rounded-lg focus:border-teal-500 focus:outline-none text-gray-800 placeholder-gray-500"
              />
              <button
                type="button"
                onClick={() => setShowConfirmPassword(!showConfirmPassword)}
                className="absolute right-4 top-1/2 transform -translate-y-1/2 text-gray-500 hover:text-gray-700"
              >
                {showConfirmPassword ? <FaEyeSlash /> : <FaEye />}
              </button>
            </div>

            {/* Terms Checkbox */}
            <div className="flex items-start gap-3 mt-6">
              <input
                type="checkbox"
                id="terms"
                checked={isAgreed}
                onChange={(e) => setIsAgreed(e.target.checked)}
                className="mt-1 w-4 h-4 text-teal-600 border-gray-300 rounded focus:ring-teal-500"
              />
              <label htmlFor="terms" className="text-sm text-gray-600">
                I agree to the{" "}
                <a href="/terms" className="text-teal-600 hover:text-teal-700">
                  Terms and Conditions
                </a>
              </label>
            </div>

            {/* Submit Button */}
            <button
              onClick={handleSubmit}
              disabled={!isAgreed}
              className="w-full py-3 mt-6 bg-gray-800 text-white font-medium rounded-lg hover:bg-gray-900 disabled:opacity-50 disabled:cursor-not-allowed transition-colors"
            >
              Create Account
            </button>
          </div>
        </div>
      </div>
    </div>
  );
};

export default AccountCreationPage;
